<template>
  <div class="cy_win">
     <div class="cy_win_con">
         <div class="line"></div>
        <!-- 示例一 -->
        <div id="myWin1" class="myWin text-c">
            <div class="item">
                <p>一 陆游《游山西村》（宋）</p>
                <p>莫笑农家腊酒浑，丰年留客足鸡豚。</p>
                <p>山重水复疑无路，柳暗花明又一村。</p>
                <p>萧鼓追随春社近，衣冠简朴古风存。</p>
                <p>从今若许闲乘月，拄杖无时夜叩门。</p>
            </div>
            <div class="item">
                <p>二 杜甫《客至》（唐）</p>
                <p>舍南舍北皆春水，但见群鸥日日来。</p>
                <p>花径不曾缘客扫，蓬门今始为君开。</p>
                <p>盘飧市远无兼味，樽酒家贫只旧醅。</p>
                <p>肯与邻翁相对饮，隔篱呼取尽馀杯！</p>
            </div>
            <div class="item">
                <p>三 蜀相 杜甫</p>
                <p>丞相祠堂何处寻？锦官城外柏森森。</p>
                <p>映阶碧草自春色，隔叶黄鹂空好音。</p>
                <p>三顾频烦天下计，两朝开济老臣心。</p>
                <p>出师未捷身先死，长使英雄泪满襟！</p>
            </div>
            <div class="item">
                <p>四 黄鹤楼 崔颢</p>
                <p>昔人已乘黄鹤去，此地空余黄鹤楼。</p>
                <p>黄鹤一去不复返，白云千载空悠悠。</p>
                <p>晴川历历汉阳树，芳草萋萋鹦鹉洲。</p>
                <p>日暮乡关何处是，烟波江上使人愁。</p>
            </div>
        </div>
<div class="codeReadonlyModule" style="height:160px">
<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
  &lt;script type="text/javascript"&gt;
    import sliderWin from "cy_static/sliderWin"; 
    sliderWin({
        elem: "#myWin",
        content: ".item"
    })
  &lt;/script&gt;
</xmp>
</div>
        <!-- 示例二 -->
        <div class="line"></div>
        <div id="myWin2" class="myWin text-c">
            <div class="item">
                <p>一 陆游《游山西村》（宋）</p>
                <p>莫笑农家腊酒浑，丰年留客足鸡豚。</p>
                <p>山重水复疑无路，柳暗花明又一村。</p>
                <p>萧鼓追随春社近，衣冠简朴古风存。</p>
                <p>从今若许闲乘月，拄杖无时夜叩门。</p>
            </div>
            <div class="item">
                <p>二 杜甫《客至》（唐）</p>
                <p>舍南舍北皆春水，但见群鸥日日来。</p>
                <p>花径不曾缘客扫，蓬门今始为君开。</p>
                <p>盘飧市远无兼味，樽酒家贫只旧醅。</p>
                <p>肯与邻翁相对饮，隔篱呼取尽馀杯！</p>
            </div>
            <div class="item">
                <p>三 蜀相 杜甫</p>
                <p>丞相祠堂何处寻？锦官城外柏森森。</p>
                <p>映阶碧草自春色，隔叶黄鹂空好音。</p>
                <p>三顾频烦天下计，两朝开济老臣心。</p>
                <p>出师未捷身先死，长使英雄泪满襟！</p>
            </div>
            <div class="item">
                <p>四 黄鹤楼 崔颢</p>
                <p>昔人已乘黄鹤去，此地空余黄鹤楼。</p>
                <p>黄鹤一去不复返，白云千载空悠悠。</p>
                <p>晴川历历汉阳树，芳草萋萋鹦鹉洲。</p>
                <p>日暮乡关何处是，烟波江上使人愁。</p>
            </div>
        </div>
<div class="codeReadonlyModule" style="height:190px">
<codemirror ref="myEditor2" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal2" style="display:none">
  &lt;script type="text/javascript"&gt;
    import sliderWin from "cy_static/sliderWin"; 
    sliderWin({
        elem: "#myWin2",
        content: ".item",
        currIndex: 1,
        isOverMove: false
    })
  &lt;/script&gt;
</xmp>
</div>
        <div class="line"></div>        
     </div>       
  </div>
</template>

<script>
  import sliderScroll from "cy_static/sliderScroll";
  import sliderWin from "cy_static/sliderWin";
  import {trim} from "../../../assets/js/codeStyle";
  export default {
    name: 'example',
    data () {
      return {
        outWin : null,// 窗口
        cmOptions:{
              readOnly:"true",
              mode : "text/html",  // 模式
              indentUnit : 2,  // 缩进单位，默认2
              smartIndent : true,  // 是否智能缩进
              tabSize : 4,  // Tab缩进，默认4
              showCursorWhenSelecting : true

        }
      }
    },
    mounted(){
        this.initOutWin();
        this.example1();
        this.example2();

        var m = this;
        ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
            var dom, valDom, index = 0, getDom = function() {
                index++;
                dom = m.$refs[element + index];
                valDom = m.$refs[element + "Val" + index];
            };
            getDom();
            while (dom) {
                dom.codemirror.setValue(trim(valDom.innerHTML));
                getDom();
            }
        });
    },
    methods: {         
        initOutWin : function () {
            var m = this, cy_win = document.querySelector('.cy_win');   
            if (m.outWin) return;             
            m.outWin = sliderScroll({
                elem: cy_win,// 窗口元素
                content: ".cy_win_con",
                isTopEffect: false,
                isBotEffect: false,
                isLockfull: true,
                pushDown: {}// 底部加载
            });
            m.outWin.isEndPull(true);
            m.outWin.isEndPush(true);
        },
        example1 () {
            sliderWin({
				  elem: "#myWin1",
                  content: ".item"
            })
        },
        example2 () {
            sliderWin({
                elem: "#myWin2",
                content: ".item",
                currIndex: 1,
                isOverMove: false
            })
        }
    },
    activated () {
        this.outWin.setActivated(true);
    },
    deactivated(){
        this.outWin.setActivated(false);
    },
    beforeDestroy() {
        this.outWin.destroy();
    }
  }
</script> 

  <style scoped>
  .cy_win{}
  .codeModule {padding: 5px 8px;}
  .myWin{background-color:#fff;cursor: e-resize;position:relative;overflow:hidden;height: 120px;border-top: 1px solid #f1f4f8;border-bottom: 1px solid #f1f4f8;}
  .myWin .item{min-height: 100px;line-height: 1.9;padding-top: 5px;background-color: #d9d9d9;}
 

  </style>


